import React from "react";
import { $ } from "react-jquery-plugin";
import { Link, NavLink } from "react-router-dom";

function Navbar() {
  $(document).ready(function () {
    // $(".BtnDrop-Menu").click(function () {
    //   $(".DropedMenu").slideDown("slow");
    // });
    // $(".BtnDrop-Menu").click(function () {
    //   $(".DropedMenu").slideToggle("slow");
    // });
  });
  return (
    <div className="navbar Navbar-Menu ">
      <div className="logo">
        <Link to="/">
          <span className="sp1">مكت</span>
          <span className="sp2">بتي</span>
        </Link>
      </div>
      <ul className="menu">
        <div className="catigoris">
          <i className="bi bi-list"></i>
          الفئات
          <i class="bi bi-caret-down-fill"></i>
          <ul className="DropedCatigories">
            <li>التاريخ الاسلامي</li>
            <li>حرب</li>
            <li>حكايات شعبيه</li>
            <li>فزياء وكيمياء</li>
            <li>الأغاني </li>
            <li>شخصيات </li>
            <li>الكون والفضاء </li>
            <li> تكنولوجيا </li>
          </ul>
        </div>

        <li>
          <NavLink to="/">الرئيسية</NavLink>
        </li>
        <li>
          <NavLink to="/Shop">تسوق</NavLink>
        </li>
        <li className="dropedBtn">
          <NavLink to="">
            المشتريات والدفع <i class="bi bi-caret-down-fill"></i>
          </NavLink>
          <div className="dropedBox">
            <Link to="/Cart">عربة التسوق</Link>
            <Link to="/Checkout">الدفع</Link>
          </div>
        </li>

        <li>
          <NavLink to="/Contact">تواصل معنا</NavLink>
        </li>
        <li>
          <NavLink to="/Stories">قصص وحواديتت</NavLink>
        </li>
      </ul>

      <div className="Icons-box">
        <Link to="/Cart">
          <i class="bi bi-cart-fill cart-Icon" data-length="0"></i>
        </Link>
        <Link to="/Favorite">
          <i class="bi bi-heart-fill fav-icon" data-length="0"></i>
        </Link>
      </div>
      <i className="bi bi-list BtnDrop-Menu"></i>
      <ul className="DropedMenu jqDrop ">
        <li>
          <NavLink to="/">الرئيسية</NavLink>
        </li>
        <li>
          <NavLink to="/Shop">تسوق</NavLink>
        </li>
        <li className="dropedBtn Carts-check-Menu-Btn">
          <NavLink to="">
            المشتريات والدفع <i class="bi bi-caret-down-fill"></i>
          </NavLink>
          <div className="dropedBox Carts-check-Menu">
            <Link to="/Cart">عربة التسوق</Link>
            <Link to="/Checkout">الدفع</Link>
          </div>
        </li>

        <li>
          <NavLink to="/Contact">تواصل معنا</NavLink>
        </li>
        <li>
          <NavLink to="/Stories">قصص وحواديتت</NavLink>
        </li>
      </ul>
    </div>
  );
}

export default Navbar;
